import { formatDate } from '@angular/common';
export class Profile {
        public id: any;
        public first_name: string;
        public middle_name: string;
        public last_name: string;
        public address: string;
        public gender: string;
        public date_of_birth: string;
        public religion: string;
        public phone_number: string;
        public email: string;
        public application_date: any;
        public photo: any;
        public medical_history: string;
        public blood_group: string;
        public genotype: string;
        public height: string;
        public weight: string;
        public application_date_approved: string;
        public student_app_submited: any;


    constructor(profile) {
        this.id = profile.id || "";
        this.photo = profile.photo || 'assets/images/user/user1.jpg';
        this.first_name = profile.first_name || '';
        this.middle_name = profile.middle_name || '';
        this.last_name = profile.last_name || '';
        this.address = profile.address || '';
        this.gender = profile.gender || '';
        this.date_of_birth = formatDate(new Date(), 'yyyy-MM-dd', 'en') || '';
        this.religion = profile.religion || '';
        this.phone_number = profile.phone_number || '';
        this.email = profile.email || '',
        this.application_date = formatDate(new Date(), 'yyyy-MM-dd', 'en') || '';
        this.medical_history = profile.medical_history || '';
        this.blood_group = profile.blood_group || '';
        this.genotype = profile.genotype || '';
        this.height = profile.height || '';
        this.weight = profile.weight || '';
        this.application_date_approved = formatDate(new Date(), 'yyyy-MM-dd', 'en') || '';
        this.student_app_submited = profile.student_app_submited || false

      }

 

    //   public getRandomID(): string {
    //     const S4 = () => {
    //       return (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1);
    //     };
    //     return S4() + S4();
    //   }
}

